body {
  font-family: ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.ctrl-main {
  display: flex;
  flex-direction: row;
}

.ctrl-controller-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.controller-item {
  display: flex;
  height: 100%;
  width: 100%;
}

.controller-item .inside {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  border-radius: 0.25rem;
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(209 213 219 / var(--tw-border-opacity, 1));
  padding: 0.25rem;
}

.controller-item .inside .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgb(209 213 219 / var(--tw-border-opacity, 1));
  padding: 0.5rem;
}

input, select {
  color: black;
}

